import { Injectable } from "@angular/core";
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor, HttpErrorResponse } from "@angular/common/http";
import { catchError, Observable, throwError } from 'rxjs';
import { Router } from "@angular/router";

@Injectable()
export class AuthInterceptor implements HttpInterceptor {

    constructor(private router : Router) {}


    intercept(request: HttpRequest<any>, next: HttpHandler) : Observable<HttpEvent<any>> {
        // Rcuprer le token stock dans le localStorage
        const token = localStorage.getItem('token');
        if (token) {
            //Ajouter le token  l'en-tte Authorization
            request = request.clone({
                setHeaders: {
                    Authorization: `Bearer ${token}`,
                    'Cache-Control': 'no-store, no-cache, must-revalidate, proxy-revalidate',
                },
            });
            // Log pour vrifier que le token est bien ajout 
            console.log("Token ajout dans l'en-tte: ", request.headers.get("Authorization"));
        }
        return next.handle(request).pipe(
            catchError((error : HttpErrorResponse) => {
                if (error.status === 401 || error.status === 403) {
                    // Non autoris, redirection vers la page de login
                    localStorage.removeItem('token');
                    localStorage.removeItem('role');
                    this.router.navigate(['/auth']);
                }
                return throwError(() => error);
            })
        );
    }
}
